body {
	background-color: #fff;
	padding: 0px 50px 50px 50px;
	/* font-family: 'Marck Script', cursive;
	font-family: 'Pompiere', cursive;
	font-family: 'Quicksand', sans-serif;
	font-family: 'Wire One', sans-serif; */
}

.aboutIcons {
	height: 50px;
	padding: 10px 20px 20px 20px;
}

.pujanInfo {
	text-align: left;
}
.logoMain {
	height: 7vh;
	margin-left: 10px;
	margin-top: 10px;
	margin-bottom: none;
}
.logoMain:hover {
	opacity: 1;
}
.pujanPhoto:hover {
	opacity: 1;
}
.pujanPhoto {
	height: 40vh;
	margin-top: 30px;
	border-radius: 100%;
}

.iconsForAbout {
	text-align: left;
}
.artistStatementText {
	display: block;
	width: 30vw;
	font-family: 'Quicksand', sans-serif;
	text-align: left;
	text-justify: inter-word;
	margin-left: 20px;
	font-size: 1.1em;
}
h2 {
	margin-top: 10px;
}

.App {
	text-align: center;
}

.divWrapper {
	padding-top: 14vh;
}

a {
	text-decoration: none;
	color: #333;
	font-family: 'Wire One', sans-serif;
	font-size: calc(5px + 2vmin);
	list-style-type: none;
	margin: none;
}

a:hover {
	color: #84b1ed;
}

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
}

li {
	float: left;
}

li a {
	display: block;
	color: #333;
	text-align: center;
	padding: 0px 20px 0px 20px;
	text-decoration: none;
}

.nameHeader {
	padding-left: 20px;
	margin-bottom: 0px;
	color: #ee7785;
	font-family: 'Marck Script', cursive;
}

.App-header {
	background-color: #fff;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	font-size: calc(5px + 1.5vmin);
	color: #333;
	position: fixed !important;
	top: 0 !important;
	z-index: 1;
	height: 14vh;
	width: 100vw;
}

.App-link {
	color: #61dafb;
}

img:hover {
	opacity: 0.8;
}
